<template>
  <div id="inviteDetail" v-if="jobData.length != 0">
    <div class="list1 clearfloat">
      <div class="l1-title floatl">
        <div class="l1-title1"></div>
        <div class="l1-title2">80</div>
      </div>
      <div class="l1-search floatr" style="display: none;">
        <form action="search01" class="clearfloat">
          <div class="input floatl">
            <input type="text" placeholder="">
          </div>
          <div class="btn floatl">
            <button></button>
          </div>
        </form>
      </div>
      <div class="l1-return">
        <span class="icon"></span><span></span>
      </div>
    </div>
    <div class="list2">
      <div>
        <div class="l2-info">
          <div class="info-1">
            <span>{{jobData[0].name}}</span><span class="small">{{jobData[0].ename}}</span>
          </div>
          <div class="info-2 clearfloat">
            <div class="i2-1">
              <i></i><span>{{jobData[0].num}}</span>
            </div>
            <div class="i2-2">
              <i></i><span>{{jobData[0].address}}</span>
            </div>
            <div class="i2-3">
              <i></i><span>{{jobData[0].time|time04}}</span>
            </div>
          </div>
        </div>
        <div class="l2-button">
          <button @click="showPop"></button>
        </div>
      </div>
    </div>
    <div class="list3">
      <div class="l3-1">
        <div class="l31-title"></div>
        <ul>
          <li v-html="jobData[0].desc"></li>
          <!--<li>1</li>-->
          <!--<li>2</li>-->
          <!--<li>3</li>-->
          <!--<li>4</li>-->
          <!--<li>5</li>-->
        </ul>
      </div>
      <div class="l3-2">
        <img :src="$baseLink+jobData[0].dpic" alt="">
      </div>
    </div>
    <div class="list4">
      <div class="l3-2">
        <img :src="$baseLink+jobData[0].rpic" alt="">
      </div>
      <div class="l3-1">
        <div class="l31-title"></div>
        <ul>
          <li v-html="jobData[0].require"></li>
          <!--<li>131</li>-->
          <!--<li>2</li>-->
          <!--<li>3</li>-->
          <!--<li>4,,</li>-->
        </ul>
      </div>
    </div>
    <div class="list5">
      <div class="l5-1">
        <div class="l5-bj"></div>
        <div class="l5-bj02"></div>
      </div>
      <div class="l5-2">
        <div class="swiper-container swiper09">
          <div class="swiper-wrapper">
            <div class="swiper-slide">
              <div class="slide-img">
                <img :src="$baseLink+jobData[0].ppic" alt="">
                <div class="img-bottom"></div>
              </div>
              <div class="slide-info">
                <div class="info-title"></div>
                <ul>
                  <li v-html="jobData[0].pay"></li>
                  <!--<li>18-12=++++++</li>-->
                  <!--<li>2</li>-->
                  <!--<li>3</li>-->
                </ul>
              </div>
            </div>
            <div class="swiper-slide">
              <div class="slide-img">
                <img :src="$baseLink+jobData[0].ppic" alt="">
                <div class="img-bottom"></div>
              </div>
              <div class="slide-info">
                <div class="info-title"></div>
                <ul>
                  <li v-html="jobData[0].pay"></li>
                  <!--<li>18-12=++++++</li>-->
                  <!--<li>2</li>-->
                  <!--<li>3</li>-->
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div class="swiper-button-prev prev09"></div>
        <div class="swiper-button-next next09"></div>
      </div>
    </div>
    <div class="popUp" v-if="popUp01">
      <div class="popUpWrap">
        <div class="wrapList">
          <div class="list-left">
            <img src="../../static/img/bj10.png" alt="">
          </div>
          <div class="list-right">
            <div>
              <div class="close01" @click="close"></div>
              <div class="data-1">
                <div class="d1-1">
                  <span class="name01"></span>
                  <input type="text" placeholder="" v-model="formData.name">
                </div>
                <div class="d1-2">
                  <span class="name01"></span>
                  <div class="select-1">
                    <div class="cir">
                      <input type="radio" @click="selInput" name="sex" value="" v-model="formData.sex">
                    </div>
                    <div class="text-1 name01"></div>
                  </div>
                  <div class="select-1">
                    <div class="cir">
                      <input type="radio" @click="selInput" name="sex" value="" v-model="formData.sex">
                    </div>
                    <div class="text-1 name01"></div>
                  </div>
                </div>
              </div>
              <div class="data-1">
                <div class="d1-1">
                  <span class="name01"></span>
                  <input type="text" placeholder="" v-model="formData.phone">
                </div>
                <div class="d1-1">
                  <span class="name01"></span>
                  <input type="text" placeholder="" v-model="formData.address">
                </div>
              </div>
              <div class="data-1 data-3">
                <div class="d1-1">
                  <span class="name01"></span>
                  <input type="text" placeholder="" v-model="formData.desc">
                </div>
              </div>
              <div class="data-4">
                <div class="d4-text name01"></div>
                <div class="d4-upload">
                  <div class="showBtn">
                    <input class="file" type="file" @change="updata">
                  </div>
                  <div class="showData">
                    <input class="text" disabled type="text" v-model="formData.upload.name">
                  </div>
                </div>
              </div>
              <div class="data-5">
                <button @click="btn"></button>
              </div>
              <div class="bj01">
                <img src="../../static/img/popImg01.png" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "inviteDetail",
      data(){
        return {
          popUp01:false,
          jobData:[],
          id:0,
          formData:{
            name:'',
            phone:'',
            address:'',
            desc:'',
            sex:'',
            upload:{}
          }
        }
      },
      filters:{
        time04(val){
          var Y,M,D,month;
          var date = new Date(val * 1000);//10*1000131000
          Y = date.getFullYear();
          M = (date.getMonth()+1 < 10 ? (date.getMonth()+1) : date.getMonth()+1);
          D = date.getDate();
          return Y+'-'+M+'-'+D;
        }
      },
      methods:{
        close(){
          this.popUp01 = false;
        },
        updata(e){
          console.log(e.target.files);
          // this.formData.upload = e.target.files[0].name;
          console.log(this.formData.upload);
          var formData1 = new FormData();
          // this.formData.upload = e.target.files[0];
          formData1.file = e.target.files[0];
          this.formData.upload = formData1;
          console.log(formData1);
        },
        btn(){
          const that = this;
          console.log(this.formData);
          var formData = this.formData;
          if(formData.sex == ''){
           formData.sex = 1;
          }else if(formData.sex == ''){
            formData.sex = 0;
          }
          console.log(formData);
          this.$api.axiosPost('/index/join/postApply',0,formData,function (data) {
            console.log(data);
            that.$alert(data.data.msg,'', {
              confirmButtonText: '',
              showClose:false
            });
          })
        },
        selInput(e){
          console.log(e);
          $('.cir').removeClass('sel');
          $(e.target).parent('.cir').addClass('sel');
        },
        showPop(){
          this.popUp01 = true;
        }
      },
      mounted(){
        const _this = this;
        this.id = this.$router.history.current.query.id;
        console.log(this.id);
        // 
        this.$api.axiosGet('/index/join/getJob/location/1/id/'+_this.id,{},function (data) {
          console.log(data);
          _this.jobData = data.data.job;
          setTimeout(function () {
            var swiper09 = new Swiper('.swiper09',{
              prevButton:'.prev09',
              nextButton:'.next09',
              observer:true
            });
          },100);
        });
      }
    }
</script>

<style scoped>
@import "../../static/css/inviteDetail.css";
</style>
<style>
  @import "../../static/css/public.css";
</style>



