<template>
  <div id="app">
    <header class="header">
      <div class="header-wrap">
        <div class="logo">
          <img src="../static/img/logo.png" alt="">
        </div>
        <nav class="header-nav">
          <ul class="nav-btn clearfloat">
            <li v-for="(item,index) in navList" :key="index" :class="{sel:index == selStatus}">
              <router-link :to="item.url">{{item.text}}</router-link>
              <ul class="nav-tags">
                <li v-for="(item1,index1) in item.tags" :key="index1" @click="nav2Btn(index)">
                  <router-link :to="item1.url">{{item1.tagsText}}</router-link>
                </li>
              </ul>
            </li>
          </ul>
        </nav>
      </div>
    </header>
    <section class="main">
    <router-view/>
      <!--<keep-alive>-->
        <!--<router-view v-if="$route.meta.keepAlive">-->
          <!--&lt;!&ndash;  page1,page2 &ndash;&gt;-->
        <!--</router-view>-->
      <!--</keep-alive>-->
      <!--<router-view v-if="!$route.meta.keepAlive">-->
        <!--&lt;!&ndash;  page3 &ndash;&gt;-->
      <!--</router-view>-->
    </section>
    <footer class="footer">
      <div class="footer-wrap">
        <div class="clearfloat">
          <div class="footer-left floatl">
            <div class="logo01">
              <img src="../static/img/logo01.png" alt="">
            </div>
            <ul v-if="bottomDetail.length != 0">
              <li>{{bottomDetail[0].tel}}&nbsp;&nbsp;&nbsp;&nbsp;{{bottomDetail[0].email}}</li>
              <li>{{bottomDetail[0].code}}</li>
              <li>{{bottomDetail[0].address}}</li>
            </ul>
          </div>
          <div class="footer-right floatr clearfloat">
            <div class="code01 floatl">
              <div class="img" v-if="bottomDetail.length != 0">
                <img :src="$baseLink+bottomDetail[0].pics"/>
              </div>
              <div></div>
            </div>
            <div class="code02 floatl">
              <div class="img" v-if="bottomDetail.length != 0">
                <img :src="$baseLink+bottomDetail[0].pic"/>
              </div>
              <div></div>
            </div>
          </div>
        </div>
      </div>
      <div class="footer-wrap1" v-if="bottomDetail.length != 0">
        <span>Copyright 2000-2011 , {{bottomDetail[0].record}} All Rights Reserved</span>
      </div>
    </footer>
  </div>
</template>
<script>
export default {
  name: 'App',
  data(){
    return {
      selStatus:0,
      navList:[
        {text:'',url:'/homePage',tags:[]},
        {text:'',url:'/aboutUs',tags:[{tagsText:'',url:'/aboutUs/aboutUsIntro'},{tagsText:'',url:'/aboutUs/aboutUsCourse'},{tagsText:'',url:'/aboutUs/aboutUsHonor'},{tagsText:'',url:'/aboutUs/aboutUsCase'},{tagsText:'',url:'/aboutUs/aboutUsContact'}]},
        {text:'',url:'/productCenter/productInfo?id=40',tags:[]},
        {text:'',url:'/productCenter/productInfo?id=41',tags:[]},
        {text:'',url:'/information',tags:[]},
        {text:'',url:'/service',tags:[{tagsText:'',url:'/service/serviceShop'},{tagsText:'',url:'/black'}]},
        {text:'',url:'/invite',tags:[{tagsText:'',url:'/invite/inviteInfo'},{tagsText:'',url:'/inviteJoin'}]},
        {text:'',url:'/partner',tags:[{tagsText:'',url:'/partner/partnerPolicy'},{tagsText:'',url:'/partner/partnerInstall'}]},
      ],
      bottomDetail:[],
      tags:[],
      //
      jj:{},
      // 
      wj:{},
      // 
      af:{}
    }
  },
  methods:{
    nav2Btn(i){
      // console.log(i);
      this.selStatus = i;
    }
  },
  watch:{
    '$route':function (to, form) {
      const that = this;
      console.log(to);
      switch (to.path) {
        case "/homePage":
          this.selStatus = 0;
          break;
        case "/aboutUs/aboutUsIntro":
          this.selStatus = 1;
          break;
        case '/aboutUs/aboutUsCourse':
          this.selStatus = 1;
          break;
        case '/aboutUs/aboutUsHonor':
          this.selStatus = 1;
          break;
        case '/aboutUs/aboutUsCase':
          this.selStatus = 1;
          break;
        case '/aboutUs/aboutUsContact':
          this.selStatus = 1;
          break;
        case "/productCenter/productInfo":
          if(to.query.id){
            if(to.query.id == 41){
              this.selStatus = 3;

            }else{
              this.selStatus = 2;

            }
          }
          break;
        case '/productCenter/ProductList':
          if(to.query.item.pid == 41){
            this.selStatus = 3;
          }else{
            this.selStatus = 2;
          }
          break;
        case '/productDetail':
          this.selStatus = 2;
          break;
        case "/information/informationNews":
          this.selStatus = 4;
          break;
        case '/information/informationDetail':
          this.selStatus = 4;
          break;
        case "/service/serviceShop":
          this.selStatus = 5;
          break;
        case '/service/serviceMessage':
          this.selStatus = 5;

          break;
        case "/invite/inviteInfo":
          this.selStatus = 6;
          break;
        case '/invite/inviteDetail':
          this.selStatus = 6;
          break;
        case '/inviteJoin':
          this.selStatus = 6;
          break;
        case "/partner/partnerPolicy":
          this.selStatus = 7;
          break;
        case '/partner/partnerInstall':
          this.selStatus = 7;
          setTimeout(function () {
            that.load = that.$loading({
              lock: true,
              text: '...',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
          },10);
          setTimeout(() => {
            that.load.close();
          }, 4000);
          break;
      }
      // console.log(this.selStatus)
    },
    selStatus:function (i) {
      sessionStorage.setItem('selStatus',i)
    }
  },
  mounted(){
    const that = this;
    console.log('pc/location/1');
    // 
    $('.nav-btn>li a').on('click',function () {
      $('.nav-btn>li').removeClass('sel');
      $(this).parent('li').addClass('sel')
    });
    var to = this.$router.history.current;
    switch (to.path) {
      case "/homePage":
        this.selStatus = 0;
        break;
      case "/aboutUs/aboutUsIntro":
        this.selStatus = 1;
        break;
      case '/aboutUs/aboutUsCourse':
        this.selStatus = 1;
        break;
      case '/aboutUs/aboutUsHonor':
        this.selStatus = 1;
        break;
      case '/aboutUs/aboutUsCase':
        this.selStatus = 1;
        break;
      case '/aboutUs/aboutUsContact':
        this.selStatus = 1;
        break;
      case "/productCenter/productInfo":
        this.selStatus = 2;
        break;
      case '/productCenter/ProductList':
        this.selStatus = 2;
        break;
      case '/productDetail':
        this.selStatus = 2;
        break;
      case "/information/informationNews":
        this.selStatus = 3;
        break;
      case '/information/informationDetail':
        this.selStatus = 3;
        break;
      case "/service/serviceShop":
        this.selStatus = 4;
        break;
      case '/service/serviceMessage':
        this.selStatus = 4;
        break;
      case "/invite/inviteInfo":
        this.selStatus = 5;
        break;
      case '/invite/inviteDetail':
        this.selStatus = 5;
        break;
      case '/inviteJoin':
        this.selStatus = 5;
        break;
      case "/partner/partnerPolicy":
        this.selStatus = 6;
        break;
      case '/partner/partnerInstall':
        this.selStatus = 6;
        break;
    }
    // 
    this.$api.axiosGet('/index/about/getLink',{},function (data) {
      // console.log(data);
      that.bottomDetail = data.data.link;
    })
    // 
    // this.$api.axiosGet('/index/product/getCates/pid/0',{},function (data) {
    //   console.log(data);
    //   that.tags = data.data.cates;
    //   for(var i = 0;i<data.data.cates;i++){
    //     if(data.data.cates[i].name == ''){
    //       that.jj = data.data.cates[i]
    //     }else if(data.data.cates[i].name == ''){
    //       that.wj = data.data.cates[i]
    //     }else{
    //       that.af = data.data.cates[i]
    //     }
    //   }
    // })
    // console.log(this.selStatus)
  }
}
</script>

<style scoped>
@import "../static/css/public.css";
</style>



